.page-nav {
    display: flex;
    flex-direction: column;
    width: 800px;
    height: 475px;
    padding-left: 124px;
    padding-top: 44px;
    border-radius: 0 0 390px 390px;
    background-color: @dark-green;
    color: @white;
    text-transform: uppercase;
    z-index: 1;
}

.page-nav__item {
    position: relative;
    font-size: 13px;
    cursor: pointer;

    &:not(:last-child) {
        margin-bottom: 25px;
    }

    &::before {
        content: "";
        position: absolute;
        display: none;
    left: -21px; 
    bottom: 0;
    border: 7px solid transparent; 
    border-left: 7px solid @white;
    }
}

.page-nav__item:hover {
    opacity: 0.7;
}

.page-nav__item:hover::before {
    display: block;
}

.page-nav__item:active {
    opacity: 0.3;
}

@media screen and (max-width: 1200px) {
    .page-nav {
        width: 36%;
        height: 400px;
        padding-left: 120px;
        padding-top: 44px;
        border-radius: 0 0 0 160px;    
    }
}

@media screen and (max-width: 767px) {
    .page-nav {
        display: none;
    }

    .page-nav__item:not(:last-child) {
        margin-bottom: 10px;
    }
}

