.footer {
    min-height: 315px;
    color: @dark-grey;
}

.footer__wrapper {
    display: flex;
    width: 70%;
    max-width: 875px;
    min-height: 225px;

    .center;
}

.footer__contact {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 240px;
    // max-width: 400px;
    margin-right: auto;
}

.footer__logo {
    width: 170px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.footer__logo-text {
    margin-top: -20px;
    font-size: 9px;
    color: @dark-green;
}

.footer__tel {
    margin-top: 35px;
    color: @dark-grey;

    .text-27-bold;
}

.footer__mail {
    margin-top: 5px;
    font-size: 14px;
}

.footer__description {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 215px;
    // max-width: 400px;
    margin-right: 122px;
}

.footer__metro {
    min-height: 100px;
    padding-top: 40px;
    text-align: end;
    background-image: url("../img/metro-footer.png");
    background-position: center left;
    background-size: 32%;
}

.footer__week {
    display: flex;
}

.footer__day {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 25px;
    border: solid 1px @dark-grey;
    text-transform: uppercase;
}

.footer__day:not(:nth-child(5)) {
    margin-right: 4px;
}

.footer__time {
    margin-top: 10px;
}

.footer__link {
    margin-top: 27px;

    &--map {
        margin-top: 60px;
    }
}

.footer__show-price {
    display: block;
    width: 175px;
    min-height: 38px;
    background-color: @dark-grey;
    color: @white;
    border-radius: 5px;
    cursor: pointer;

    &:hover {
        color: @dark-grey;
        background-color: @white;
        border: solid 1px @dark-grey;

    }

    &:active {
        opacity: 0.7;
    }
}

.footer__social {
    width: 140px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.footer__social-list {
    display: flex;
    justify-content: space-around;
}

.footer__social-item {
    display: block;
    width: 35px;
    height: 35px;
    background-size: 100%;
    cursor: pointer;
}

.footer__social-item a {
    display: block;
}

.footer__social-item:hover {
    opacity: 0.6;
}

.footer__social-item:active {
    opacity: 0.8;
}

.footer__social-item--fb {
    background-image: url("../img/fb.png");
}

.footer__social-item--insta {
    background-image: url("../img/instagram.png");
}

.footer__social-item--vk {
    background-image: url("../img/vk.png");
}

.footer__map-link {
    display: block;
    width: 270px;
    min-height: 38px;
    margin-left: -140px;
    color: @dark-grey;
    border: solid 1px @dark-grey;
    background-color: @white;
    border-radius: 5px;
    cursor: pointer;
}

.footer__map-link:hover {
    background-color: @dark-grey;
    color: @white;
}

.footer__map-link:active {
    opacity: 0.7;
}

@media screen and (max-width: 1200px) {
    .footer__wrapper {
        flex-wrap: wrap;
        justify-content: center;
        display: flex;
        width: auto;
        padding-right: 20px;
        padding-left: 20px;
    }

    .footer__description {
        margin-right: 10px;
    }

    .footer__map-link {
        width: 180px;
        margin-left: 0;
    }

    .footer__social {
        width: 180px;
    }
}

@media screen and (max-width: 767px) {
    .footer__wrapper {
        padding-top: 20px;
    }

    .footer__contact,
    .footer__description,
    .footer__social {
        margin-bottom: 10px;
        .center;
    }

    .footer__show-price {
        width: 100%;
    }
}

