.doctors__title {
    width: 55%;
    max-width: 720px;
    margin-top: 45px;
    text-transform: uppercase;
    color: @dark-green;
    text-align: center;

    .center;
}

.doctors__wrapper {
    display: flex;
    justify-content: space-between;
    width: 82%;
    max-width: 1050px;
    margin-top: 30px;
    padding-bottom: 100px;

    .center;
}

.doctors__item {
    display: flex;
    flex-wrap: wrap;
    width: 340px;
}

.doctors__image {
    width: 130px;
    height: 130px;
}

.doctors__text {
    padding-left: 10px;
    padding-top: 23px;
    font-size: 14px;
}

.doctors__name {
    text-transform: uppercase;
}

.doctors__specialty {
    margin-top: 10px;
}

.doctors__sign {
    margin-top: 30px;
}

@media (max-width: 1200px) {
    .doctors__title {
        width: 70%;
    }
}

@media (max-width: 767px) {
    .doctors__title {
        font-size: 16px;
        width: 90%;
    }

    .doctors__text {
        font-size: 13px;
    }
}