.reviews__screen-header {
    margin-top: 30px;
}

.reviews__wrapper {
    display: flex;
    width: 82%;
    max-width: 1050px;
    // margin-top: 20px;
    justify-content: space-between;
    width: 82%;
    max-width: 1050px;
    margin-top: 0px;
    padding-bottom: 60px;

    .center;

}

.reviews__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // width: 31%;
    min-height: 387px;
    border: solid 1px @dark-grey;
    padding-top: 30px;
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 25px;
    font-size: 13px;
    color: @dark-grey;
}

// .reviews__item:not(:nth-child(3n)) {
//     margin-right: 1.56%;
// }

.reviews__text {
    margin-top: 5px;
}

.reviews__author {
    margin-top: 30px;
    color: @light-green;
}

@media screen and (max-width: 767px) {
    .reviews__wrapper {
        width: 86%;
    }
}