  background-color: rgba(0, 0, 0, 0.4);
}
.popup__wrapper {
  position: relative;
  background-color: #00807C;
  margin: 15% auto 10% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 750px;
  box-shadow: 10px 10px 0 0 rgba(0, 0, 0, 0.4);
}
.popup__close {
  position: absolute;
  right: 20px;
  top: 0;
  font-size: 46px;
  color: #ffffff;
  cursor: pointer;
}
.popup__form {
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 767px) {
  .popup__wrapper {
    width: 310px;
  }
  .popup__wrapper .form__submit {
    width: 150px;
  }
}
.mobile-menu {
  display: none;
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0px;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.mobile-menu__wrapper {
  position: relative;
  background-color: #00807C;
  border: 1px solid #ffffff;
  width: 100%;
  padding-top: 50px;
  padding-bottom: 40px;
}
.mobile-menu__closed {
  position: absolute;
  right: 30px;
  top: 30px;
  font-size: 50px;
  color: #ffffff;
  cursor: pointer;
}
.mobile-menu__list {
  text-align: center;
}
.mobile-menu__item {
  font-size: 16px;
  font-weight: bold;
  color: white;
}
.mobile-menu__item:not(:last-child) {
  margin-bottom: 10px;
}
