.services {
    padding-top: 35px;
}

.services__wrapper {
    width: 82%;
    max-width: 1050px;
    min-height: 600px;
    padding-left: 380px;
    padding-top: 26px;
    background-image: url("../img/services-bg.png");
    background-position: left top;
    background-size: 32%;

    .center;
}

.services__price {
    font-size: 13px;
    color: @dark-grey-blue;
}

.services__price tr {
    height: 28px;
}

.services__price td:first-child {
    width: 500px;
}

.services__price td:nth-child(2) {
    width: 60px;
}


.services__show-all {
    margin-top: 30px;
    margin-left: 252px;
    display: block;
}

@media screen and (max-width: 1200px) {
    .services__wrapper {
        padding-left: 25%;
    }

    .services__show-all {
        margin-left: 33%;
    }
}

@media screen and (max-width: 767px) {
.services__show-all {
    display: flex;
    margin-left: 0;
    }

    .services__price {
        font-size: 9px;
    }
 
    .services__wrapper {
        padding-left: 0;
        padding-top: 0;
        background: none;
        min-height: 390px;
    }

    .services__price tr {
        height: 20px;
    }
}
