.choice {
    position: relative;
    display: flex;
    align-items: flex-end;
    width: 800px;
    height: 550px;
    padding-left: 108px;
    border-radius: 390px 390px 0 0;
    background-color: @light-green;
    z-index: 1;

    &--second {
        background-color: @dark-green;
    }

    &--light-green {
        background-color: @light-green;
    }

    &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: -90px;
        width: 90px;
        height: 110px;
        background-color: #B5BFC9;
        border-radius: 120px 0 0 0;
    }
}

.choice__gender {
    width: 111px;
    min-height: 151px;
    padding-top: 80px;
    color: white;
    font-size: 14px;
}

.choice__gender--male {
    background-image: url(../img/male.png);
    background-position: top 10px left 10px;
    background-size: 50%;

    &:hover {
        background-image: url(../img/male-active.png);
        color: #dddddd;
    }

    &:active {
        background-size: 47%;
        color: @white;
    }

    &-active {
        background-image: url(../img/male-active.png);
    }
}

.choice__gender--female {
    background-image: url(../img/female.png);
    background-position: top 5px left 17px;
    background-size: 40%;
    
    &:hover {
        background-image: url(../img/female-active.png);
        color: #dddddd;
    }

    &:active {
        background-size: 38%;
        color: @white;
    }

    &-active {
        background-image: url(../img/female-active.png);
    }
}

@media screen and (max-width: 1200px) {
  .choice {
    width: 100%;
    height: 70px;
    max-width: 1200px;
    min-height: 70px;
    padding-left: 108px;
    border-radius: 0;

    &::before {
        display: none;
    }
  }

  .choice__gender {
    width: 156px;
    min-height: 65px;
    padding-top: 21px;
    padding-left: 50px;
    padding-right: 20px;
    color: white;
    font-size: 14px;
    text-align: end;
}

.choice__gender--male {
    background-size: 25%;

    &:hover {
        background-size: 27%;
    }

    &:active {
        background-size: 22%;
    }
}

.choice__gender--female {
    background-size: 20%;
    
    &:hover {
        background-size: 22%;
        color: #dddddd;
    }

    &:active {
        background-size: 18%;
    }
}
}

@media screen and (max-width: 1200px) {
.choice {
    padding-left: 3px;
}
}

@media screen and (max-width: 767px) {
    .choice {
        padding-left: 5%;
    }
}