.consult {
  width: 100%;
  min-height: 500px;
  background-color: @light-green;
}

.consult__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  // flex-wrap: wrap;
  max-width: 1280px;
  min-height: 702px;
}

.consult__content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 900px;
  min-height: 720px;
  padding-left: 131px;
  padding-top: 21px;
  border-radius: 0 0 525px 0;
  background-image: url('../img/consult-bg.png');
  background-position: top left;
  background-size: 100%;
}

.consult__title {
  margin-top: 16px;
  margin-bottom: 10px;
}

.consult__page-nav {
  margin-left: -50px;
  margin-right: -400px;
}

.consult__form {
  position: absolute;
  left: 65%;
  bottom: 43px;
}

@media screen and (max-width: 1200px) {
  .consult__content {
    padding-left: 50px;
    background-size: 85%;
    min-height: 660px;
  }

  .consult__form {
    bottom: 30px;
    left: auto;
    right: 14%;
  }

  .consult__page-nav {
    margin-left: -330px;
    margin-right: 0;
  }

  .consult__wrapper {
    min-height: 660px;
  }
}

@media screen and (max-width: 767px) {
  .consult {
    min-height: 0;
  }

  .consult__page-nav {
    display: none;
  }

  .consult__wrapper {
    flex-direction: column;
    min-height: 212px;
  }

  .consult__content {
    padding-left: 5%;
    background-size: 100%;
    min-height: 255px;
    width: auto;
    background-image: url("../img/consult-bg-mobile.png");
    border-radius: 0;
  }

  .consult__form {
    display: none;
  }

  .consult__title {
    margin-top: -5px;
    margin-bottom: 10px;
  }
}