.screen-header {
    position: relative;
    display: flex;
    align-items: center;
    width: 82%;
    max-width: 1050px;

    .center;
}

.screen-header__title {
    color: @white;
    text-transform: uppercase;
    margin-right: 30px;

    &--right {
        margin-left: 30px;
        margin-right: 0;
    }

    &--center {
        margin-right: 30px;
        margin-left: 30px;
    }

    &--green {
        color: @dark-green;
    }


    .text-27-bold;
}

.screen-header__line {
    flex-grow: 1;
    width: auto;
    height: 2px;
    background-color: @white;

    &--green {
        background-color: @dark-green;
    }
}

@media (max-width: 767px) {
    .screen-header {
        margin-left: 5%;
    }

    .screen-header__title {
        font-size: 20px;
    }
}