.subtitle {
    font-family: "GothamProBold", "Arial", sans-serif;
    font-size: 48px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 56px;
    letter-spacing: normal;
    color: @white;
    text-transform: uppercase;

    width: 650px;
    
    &--green {
        color: @dark-green;
    }

}

@media screen and (max-width: 767px) {
    .subtitle {
        font-size: 20px;
        line-height: 1.2;
        width: auto;
        // text-align: center;

        &--mobile-white {
            color: @white;
        }
    }
}
