.mobile-menu {
    display: none;
    position: fixed;
    z-index: 999;
    left: 0;
    top: 0px;
    width: 100%;
    height: 100%;
    overflow: auto;
}

.mobile-menu__wrapper {
    position: relative;
    background-color: #00807C;
    border: 1px solid #ffffff;
    width: 100%;
    padding-top: 50px;
    padding-bottom: 40px;
}

.mobile-menu__closed {
    position: absolute;
    right: 30px;
    top: 30px;
    font-size: 50px;
    color: #ffffff;
    cursor: pointer;
}

.mobile-menu__list {
    text-align: center;
}

.mobile-menu__item {
    font-size: 16px;
    font-weight: bold;
    color: white;

    &:not(:last-child) {
        margin-bottom: 10px;
}
}