.place {
    width: 100%;
    padding-top: 6px;
    background-color: @light-green;
    color: @white;
}

.place__wrapper {
    position: relative;
    display: flex;
    // justify-content: center;
    flex-wrap: wrap;
    max-width: 1050px;
    margin-bottom: 30px;

    .center;
  }

.place__doctor-wrapper {
    width: 432px;
    margin-left: 155px;
    padding-top: 76px;
}

.place__doctor-title {
    max-width: 385px;
    text-transform: uppercase;
    .text-24-light;
}

.place__doctor-list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 56px;
    height: 218px;
    padding-bottom: 10px;
}

.place__doctor-item {
    position: relative;
    width: 207px;
    margin-bottom: 38px;
    padding-left: 47px;
    font-size: 14px;

    &::after {
        content: "";
        position: absolute;
        top: -3px;
        left: 0;
        width: 28px;
        height: 18px;
        border: solid 1px @white;
    }

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 8px;
        width: 12px;
        height: 7px;
        transform: rotate(-45deg);
        border-left: solid 1px @white;
        border-bottom: solid 1px @white;
    }

}

.place__map {
    width: 450px;
    padding-left: 310px;
    background-image: url("../img/map.png");
    background-position: left top 19px;
    background-size: 70%;
}

.place__metro {
    width: 135px;
    padding-top: 80px;
    background-image: url("../img/metro.png");
    background-position: 28px 22px;
    background-size: 50%;
}

.place__ya-map {
    display: inline-block;
    width: 100px;
    margin-top: 180px;
    font-size: 13px;
    text-decoration: underline;
}

@media screen and (max-width: 1200px) {
    .place__wrapper {
        justify-content: center;
    }

    .place__doctor-wrapper {
        margin-left: 0;
    }

    .place__doctor-list {
        height: 175px;
    }
}

@media screen and (max-width: 767px) {
    .place__doctor-list {
        min-height: 230px;
        width: 86%;
        .center;
    }

    .place__doctor-wrapper {
        padding-top: 15px;
    }

    .place__doctor-title {
        font-size: 20px;
        text-align: center;
        margin-left: 3%;
        margin-right: 3%;
    }

    .place__doctor-item {
        margin-bottom: 20px;
    }

    .place__map {
        width: 276px;
        padding-left: 140px;
        background-size: 50%;
    }

    .place__ya-map {
        margin-top: 50px;
    }
}
