.general {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 900px;
    min-height: 720px;
    border-radius: 0 525px 0 0;
    background-color: @dark-green;

    &::after {
        content: "";
        position: absolute;
        top: 432px;
        left: 133px;
        width: 655px;
        height: 2px;
        background-color: @white;
    }

    &--second {
        border-radius: 0;
        background-color: @light-green;
        background-image: url("../img/gynecology-bg.png");
        background-size: 100%;
    }

    &--third {
        border-radius: 0;
        background-color: @dark-green;
        background-image: url("../img/urology-bg.png");
        background-size: 100%;
    }
}

.general__sign {
    margin-left: 135px;
    margin-bottom: 70px;
}

.general__title {
    margin-left: 135px;
    margin-bottom: 5px;
}

.general__subtitle {
    margin-left: 135px;
    margin-bottom: 25px;
}

.general__form {
    margin-bottom: 142px;
    margin-left: auto;
    margin-right: 78px;
}

@media screen and (max-width: 1200px) {
  .general {
      width: 100%;
      max-width: 1200px;
      min-height: 600px;

      &::after {
        content: "";
        top: 422px;
        width: 500px;
      }
    }

    .general__sign {
        margin-bottom: 40px;
    }
    

    .general__form {
        margin-left: 50px;
        margin-bottom: 40px;
    }

}

@media screen and (max-width: 767px) {
    .general::after {
        display: none;
    }

    .general__title {
        margin-left: 5%;
    }
    
    .general__subtitle {
        margin-left: 5%;
        margin-right: 5%;
    }

    .general__sign {
        .center;
    }

    .general__form {
        .center;
    }
}

