.benefits__screen-header {
    margin-top: 30px;
}

.benefits__text {
    width: 82%;
    max-width: 1050px;
    text-align: center;
    font-size: 14px;
    color: @dark-grey;

    .center;
}

.benefits__content {
    display: flex;
    flex-wrap: wrap;
    width: 82%;
    max-width: 1050px;
    margin-top: 20px;
    padding-left: 1.95%;
    padding-right: 1.95%;

    .center;
}

.benefits__item {
    display: flex;
    align-items: center;
    width: 32%;
    min-height: 120px;
}

.benefits__item:not(:nth-child(3n)) {
    margin-right: 1.56%;
}

.benefits__item-text {
    width: 200px;
    color: @dark-grey;
}

.benefits__item div {
    .center;
}

.icon-benefit1 {
    background-image: url("../img/benefit1.png");
    width: 77px;
    height: 54px;
  }
  .icon-benefit2 {
    background-image: url("../img/benefit2.png");
    width: 54px;
    height: 69px;
  }
  .icon-benefit3 {
    background-image: url("../img/benefit3.png");
    width: 70px;
    height: 53px;
  }
  .icon-benefit4 {
    background-image: url("../img/benefit4.png");
    width: 63px;
    height: 64px;
  }
  .icon-benefit5 {
    background-image: url("../img/benefit5.png");
    width: 48px;
    height: 70px;
  }
  .icon-benefit6 {
    background-image: url("../img/benefit6.png");
    width: 57px;
    height: 55px;
  }
  .icon-benefit7 {
    background-image: url("../img/benefit7.png");
    width: 62px;
    height: 61px;
  }
  .icon-benefit8 {
    background-image: url("../img/benefit8.png");
    width: 51px;
    height: 69px;
  }
  .icon-benefit9 {
    background-image: url("../img/benefit9.png");
    width: 70px;
    height: 56px;
  }
  
@media screen and (max-width: 1200px) {
    .benefits__content {
      justify-content: center;
      width: auto;
    }

    .benefits__item {
      width: 320px;
    }

    .benefits__item:not(:nth-child(3n)) {
      margin-right: 0;
    }  


  .benefits__text {
    width: 82%;
    max-width: 1050px;
    text-align: center;
    font-size: 13px;
  }
}

@media screen and (max-width: 767px) {
  .benefits__content {
    padding-left: 5%;
    padding-right: 5%;
  }
  .benefits__text {
    text-align: left;
    font-size: 13px;
  }

  .benefits__item {
    min-height: 70px;

    & div {
      background-size: 65%;
      background-position: center;
    }
  }

  .benefits__item-text {
    font-size: 13px;
  }
}