.header {
  position: absolute;
  width: 100%;
  background-color: transparent;
  z-index: 4;
}

.header__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  max-width: 1280px;
  padding: 67px 104px 10px 128px;
}

.header__sandwich {
  position: absolute;
  top: 55px;
  right: 5%;
  display: none;
  width: 50px;
  height: 5px;
  background-color: @dark-green;
  box-shadow: 0px -10px 0px 0px @dark-green, 0px 10px 0px 0px @dark-green;

  &--white {
    background-color: @white;
    box-shadow: 0px -10px 0px 0px @white, 0px 10px 0px 0px @white;
  }
}

.header__logo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.header__logo-text {
  margin-top: -20px;
  font-size: 9px;
  color: @white;
}

.header__img {
  width: 162px;
}

.header__call {
  padding-top: 9px;
  color: @dark-grey;

  &--white {
    color: @white;
  }
}

.header__phone-text {
  margin-top: 0;

  &--white {
    color: @white;
  }
}

.header__phone--white {
  color: @white;
}

.header__phone:hover {
  opacity: 0.7;
}

@media screen and (max-width: 767px) {
  .header__wrapper {
    flex-direction: column;
    padding: 35px 5% 0 5%;
  }

  .header__sandwich {
    display: block;
  }


  .header__call {
    font-size: 12px;
    color: @white;
  }
  
  .header__phone-text {
    font-size: 12px;
  }

  .header__img {
    width: 140px;
  }

  .header__logo-text {
    margin-top: -16px;
    font-size: 8px;
    color: @white;
  }
  
}
