.form {
    width: 728px;
    min-height: 117px;
    padding: 9px 30px 10px 39px;
    background-color: #ffffff;
    border-radius: 5px;
    color: @dark-grey-blue;

    &--vertical {
        width: 320px;
        color: @white;
        background-color: @light-green;
    }

    &--long {
        width: 86%;
        padding-left: 7%;
        padding-right: 7%;

        & .form__item {
            width: 35%;
        }

        & .form__submit {
            width: 175px;
            margin-left: auto;
        }

        .center;
    }

    &--green {
        background-color: @light-green;
        color: @white;

        & .form__item {
            background-color: @light-green;
            color: @white;
            border-color: @white;
            }

        & .form__submit {
            background-color: @white;
            color: @dark-grey;
            }

    }

    &--popup {
        background-color: @dark-green;
        color: @white;

        & .form__item {
            background-color: @dark-green;
            color: @white;
            border-color: @white;
            }

        & .form__submit {
            background-color: @light-green;
            color: @white;
            }

    }
}

.form__title--vertical {
    font-size: 12px;
}

.form__warning {
    margin-top: 11px;
    font-size: 10px;
}

.form__wrapper {
    display: flex;
    margin-top: 14px;
    
    &--vertical {
        flex-direction: column;
    }
}

.form__item {
    width: 275px;
    height: 32px;
    margin-right: 13px;
    text-align: center;
    border: solid 1px @dark-grey;
    border-radius: 5px;
        
    &--vertical {
        margin-bottom: 5px;
        background-color: @light-green;
        border: solid 1px @white;
        color: @white;
    }

    &:hover, &:focus {
        opacity: 0.7;
    }
}

.form__submit {
    width: 82px;
    height: 30px;
    color: @white;
    background-color: @light-green;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    &--vertical {
        width: 275px;
        background-color: @dark-green;
    }

    &:hover {
        opacity: 0.7;
    }

    &:focus {
        border: solid 2px @white;
        outline: none;
    }

}

@media screen and (max-width: 1200px) {
    .form {
        width: 650px;

        &--vertical {
            width: 345px;
        }
    }
}

@media screen and (max-width: 767px) {
    .form {
        width: 275px;
    }

    .form__wrapper {
        flex-direction: column;
    }

    .form__title {
        text-align: center;
    }

    .form__item {
        width: 205px;
        margin-bottom: 10px;
    }

    .form__submit {
        .center;
    }

    .form--long .form__item {
        width: 94%;
        margin-left: auto;
        margin-right: auto;
    }

    .form__warning {
        text-align: center;
        font-size: 9px;
    }
}

