.menu {
    background-color: @dark-green;

    &--urology {
        background-color: @light-green;
    }
}

.menu__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 55%;
    max-width: 700px;
    min-height: 40px;
    color: @white;

    .center;
}

.menu__item {
    cursor: pointer;
}

.menu__item:hover {
    border-bottom: solid 2px @white;
}

.menu__item:active {
    opacity: 0.5;
}

@media screen and (max-width: 1200px) {
    .menu__list {
        width: auto;
    }
}

@media screen and (max-width: 767px) {
    .menu {
        display: none;
    }   
}
