.btn {
    width: 195px;
    min-height: 30px;
    color: @white;
    background-color: @light-green;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    &--services {
        width: 275px;
        min-height: 35px;
        background-color: @dark-green;
    }

    &--white {
        color: @light-green;
        background-color: @white;
    }

    &:hover {
        opacity: 0.7;
    }

    &:focus {
        border: solid 2px @white;
        outline: none;
    }
}

@media (max-width: 767px) {
    .btn--services {
        .center;
    }
}
