.popup {
    display: none;
    position: fixed; 
    z-index: 5;
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    overflow: auto;
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4); 
}

.popup__wrapper {
    position: relative;
    background-color: @dark-green;
    margin: 15% auto 10% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 750px;
    box-shadow: 10px 10px 0 0 rgba(0,0,0,0.4);
}

.popup__close {
    position: absolute;
    right: 20px;
    top: 0;
    font-size: 46px;
    color: @white;
    cursor: pointer;
}

.popup__form {
    .center;
}

@media (max-width: 767px) {
    .popup__wrapper {
        width: 310px;
    }

    .popup__wrapper .form__submit {
        width: 150px;
    }
}