.content {
    background-color: @light-green;

    &--dark-green {
        background-color: @dark-green;
    }
}

.content__title {
    margin-top: 44px;
    margin-left: 135px;
}

.content__subtitle {
    font-family: "GothamProBold", "Arial", sans-serif;
    font-weight: bold;
    margin-top: 10px;
    margin-left: 135px;
    color: @white;
}

.content__disease {
    display: flex;
    justify-content: space-between;
    margin-top: 28px;
}

.content__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 275px;
    min-height: 320px;
    padding-top: 34px;
    padding-bottom: 66px;
    padding-left: 50px;
    background-color: @dark-green;
    color: @white;

    &--light {
        background-color: @light-green;
    }
}

.disease {
    position: relative;
    display: inline-block;
    width: 200px;
    padding-left: 35px;
    text-transform: uppercase;
    cursor: pointer;
}

.disease-1::before {
    content: "";
    position: absolute;
    display: none;
    left: 9px;
    bottom: 0;
    border: 10px solid transparent;
    border-left: 10px solid #ffffff;
}

.content__description {
    display: none;
    width: 75%;
    max-width: 960px;
    min-height: 320px;
    padding-left: 502px;
    padding-top: 50px;
    padding-right: 40px;
    background-position: top left;
    background-size: 430px 320px;
    background-color: @white;
    color: @dark-grey;

    & p {
        font-size: 14px;
    }
}

.content__description--disease-1 {
    background-image: url("../img/gynecology6.jpg");

    &--urology {
        background-image: url("../img/urology6.jpg");
    }
}

.content__description--disease-2 {
    background-image: url("../img/gynecology1.jpg");
    
    &--urology {
        background-image: url("../img/urology1.jpg");
    }
}

.content__description--disease-3 {
    background-image: url("../img/gynecology2.jpg");
    
    &--urology {
        background-image: url("../img/urology2.jpg");
    }
}

.content__description--disease-4 {
    background-image: url("../img/gynecology3.jpg");

    &--urology {
        background-image: url("../img/urology3.jpg");
    }
}

.content__description--disease-5 {
    background-image: url("../img/gynecology4.jpg");

    &--urology {
        background-image: url("../img/urology4.jpg");
    }
}

.content__description--disease-6 {
    background-image: url("../img/gynecology5.jpg");

    &--urology {
        background-image: url("../img/urology5.jpg");
    }
}

.radio-disease-1:checked~.content__description--disease-1 {
    display: block;
    // background-image: url("../img/disease-bg-m.png");
}

.radio-disease-2:checked~.content__description--disease-2 {
    display: block;
    // background-image: url("../img/disease-bg2.png");
}

.radio-disease-3:checked~.content__description--disease-3 {
    display: block;
    // background-image: url("../img/disease-bg.png");
}

.radio-disease-4:checked~.content__description--disease-4 {
    display: block;
    // background-image: url("../img/disease-bg2.png");
}

.radio-disease-5:checked~.content__description--disease-5 {
    display: block;
    // background-image: url("../img/disease-bg.png");
}

.radio-disease-6:checked~.content__description--disease-6 {
    display: block;
    // background-image: url("../img/disease-bg2.png");
}

.radio-disease-1:checked~.content__list li:first-child {
    background-image: url("../img/triangle.png");
}

.radio-disease-2:checked~.content__list li:nth-child(2) {
    background-image: url("../img/triangle.png");
}

.radio-disease-3:checked~.content__list li:nth-child(3) {
    background-image: url("../img/triangle.png");
}

.radio-disease-4:checked~.content__list li:nth-child(4) {
    background-image: url("../img/triangle.png");
}

.radio-disease-5:checked~.content__list li:nth-child(5) {
    background-image: url("../img/triangle.png");
}

.radio-disease-6:checked~.content__list li:nth-child(6) {
    background-image: url("../img/triangle.png");
}

.content__benefit {
    display: flex;
    justify-content: space-between;
    width: 780px;

    .center;
}

.content__benefit-item {
    position: relative;
    width: 371px;
    padding-top: 20px;
    padding-left: 50px;
    padding-right: 50px;
    color: @white;

    & h3 {
        .text-16-bold;
    }

    & p {
        font-size: 14px;
    }
}

.content__benefit-item::before {
    content: "";
    position: absolute;
    top: 44px;
    left: 8px;
    width: 12px;
    height: 7px;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    border-left: solid 1px #ffffff;
    border-bottom: solid 1px #ffffff;
}

.content__benefit-item::after {
    content: "";
    position: absolute;
    top: 41px;
    left: 0;
    width: 28px;
    height: 18px;
    border: solid 1px #ffffff;
}

.content__line {
    width: 500px;
    height: 1px;
    margin-top: 20px;
    background-color: @white;

    .center;
}

.content__text {
    width: 780px;
    margin-top: 30px;
    font-size: 14px;
    color: @white;

    .center;

    &--left {
        margin-left: 135px;
    }
}

.content__form-wrapper {
    width: 82%;
    margin-top: 50px;
    margin-bottom: 65px;

    .center;
}

@media screen and (max-width: 1200px) {
    .content__description {
        max-width: 450px;
        min-height: 600px;
        padding-top: 317px;
        padding-right: 20px;
        padding-left: 20px;
        background-size: 450px 300px;
    }

    .content__disease {
        justify-content: space-around;
    }

    .content__benefit {
        justify-content: space-around;
        width: 700px;
    }

    .content__text {
        width: 680px;

        &-left {
            margin-right: 5%;
        }
    }
}

@media screen and (max-width: 767px) {
    .content__disease {
        flex-direction: column;
    }

    .content__benefit {
        flex-wrap: wrap;
        width: 80%;
    }

    .content__text {
        width: 90%;

        &--left {
            margin-left: 5%;
        }
    }

    .content__line {
        width: 70%;
    }

    .content__title {
        margin-left: 15px;
        margin-right: 15px;
    }

    .content__subtitle {
        margin-left: 15px;
        margin-right: 15px;
    }

    .content__list {
        width: 100%;
        max-width: 450px;
        flex-wrap: wrap;
        height: 180px;
        min-height: 0;
        padding-bottom: 37px;
        padding-left: 6.5%;
        margin-bottom: 25px;
        
        & li {
            width: 150px;
        }


        .center;
    }

    .disease {
        width: 149px;
        font-size: 13px;
        padding-left: 20px;
        margin-bottom: 10px;
    }

    .content__description {
        width: 100%;
        max-width: 450px;

        .center;
    }
}
