.article {
    width: 325px;
    padding-top: 183px;
    background-size: 100% auto;
    background-position: top;

    &:not(:last-child) {
        margin-right: 35px;
    }

    &--one {
        background-image: url(../img/advantage1.png);
    }

    &--two {
        background-image: url(../img/advantage2.png);
    }

    &--three {
        background-image: url(../img/advantage3.png);
    }

}

.article__wrapper {
    min-height: 293px;
    padding-top: 33px;
    padding-bottom: 20px;
    padding-left: 30px;
    padding-right: 30px;
    border: solid 1px @white;
}

.article__title {
    color: @white;
    margin-bottom: 15px;
}

.article__text {
    font-size: 13px;
    color: @white;
}

@media screen and (max-width: 1200px) {
    .article {
        width: 700px;
        padding-left: 286px;
        background-size: 37%;
        background-position: center left 25px;
        border: solid 1px white;
        padding-top: 0;
        margin-bottom: 15px;
    }

    .article__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        border: none;
    }

    .article:not(:last-child) {
        margin-right: 0;
    }
}

@media screen and (max-width: 767px) {
    .article {
        width: 275px;
        padding-top: 183px;
        padding-left: 0;
        background-size: 100% auto;
        background-position: top;

        border: none;
        padding-top: 0;
        margin-bottom: 15px;
    }

    .article__wrapper {
        justify-content: flex-start;
        min-height: 300px;
        margin-top: 155px;
        padding-top: 20px;
        padding-bottom: 20px;
        padding-left: 30px;
        padding-right: 30px;
        border: solid 1px @white;

        // display: flex;
        // flex-direction: column;
        // justify-content: center;
        // border: none;
    }

    .article__text {
        margin-bottom: 10px;
    }

    .article:not(:last-child) {
        margin-right: 0;
    }
}
